import React from 'react';
import Col from 'react-bootstrap/Col';
import { Card } from 'react-bootstrap';

export const CharacterItem = ( props ) => {
    return (
        <Col xs={ 12 } sm= { 12 } md={ 6 } lg={ 4 } xl={ 4 }>
            <Card id="cards">
                <Card.Body>
                <Card.Img src={ require(`../../assets/images/${ props.image }`).default }></Card.Img>
                    <Card.Title><h2>{ props.name }</h2></Card.Title>
                    <Card.Text><p>First appearance: { props.year }</p></Card.Text>
                    <Card.Text><p>{ props.description }</p></Card.Text>
                </Card.Body>
            </Card>
        </Col>
    )
} 