import React from 'react';
import Row from 'react-bootstrap/Row';
import { LibraryItem } from './LibraryItem';

export const LibraryList = ( props ) => {
    function getLibrarys(){
        return props.librarys.map( (library, i ) => {
                return <LibraryItem
                    key={`ff-${i}`}
                    image={ library.image }
                />
        } );
    }

    return (
        <section id="game-library">
            <h1>Explore the game library</h1>
            <Row>    
                { getLibrarys() }
            </Row>
        </section>
    )
} 