import React from 'react';
import Row from 'react-bootstrap/Row';
import { GameItem } from './GameItem';

export const GameList = ( props ) => {
    function getGames(){
        return props.games.map( (game, i ) => {
                return <GameItem
                    key={`ff-${i}`}
                    name={ game.name }
                    image={ game.image }
                    year={ game.year }
                    description={ game.description }
                    category={ game.category }
                />
        } );
    }

    return (
        <section>
            <h1>Game library</h1>
            <Row>    
                { getGames() }
            </Row>
        </section>
    )
} 